import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import {
  Header,
  Nav,
  RowFlex,
  Footer,
  Podcasts
} from './../containers';

import {
  homeActions,
  homeSelectors
} from './../redux/ducks/home';

import {
  Survey
} from './../components';

const readerSurvey = {
  headliner: 'Header',
  greetA: 'Greeting',
  greetB: 'Subject',
  text: 'Call to action text',
  btnTxt: 'Button',
  btnSubTxt: '(auxiliary text)'
};


class PodcastRoute extends PureComponent {
  componentDidUpdate () {
    const { navOverlay } = this.props.ui;
    {
      document.body.style.overflow = navOverlay ? 'hidden' : '';
    }
  }
  render () {
    return (
      <>
        <section className="container">
          <Header {...this.props} />
          <Nav />
        </section>
        <section className="container">
          <RowFlex cls="row-flex">
            <div className="col">
              <section className="cluster">
                <header className="cluster__header">
                  <h2 className="cluster__label header--style-3">Podcasts</h2>
                </header>
              </section>
              <Podcasts />
            </div>
            <div className="col-auto hidden-sm-down">
              <aside className="sidebar">
                <section className="sidebar__section">
                  <div className="banner">
                    <div style={{overflow: 'hidden'}} className="banner-inner">
                      <Survey {...readerSurvey} />
                    </div>
                  </div>
                </section>
              </aside>
            </div>
          </RowFlex>
        </section>
        <section className="container">
          <Footer />
        </section>
      </>
    );
  }
}

PodcastRoute.propTypes = {
  ui: PropTypes.object
};

const mapActionsToProps = {...homeActions};

const mapStateToProps = state => ({
  ui: homeSelectors.getHomeUI(state),
});

export default connect(
  mapStateToProps,
  mapActionsToProps
)(PodcastRoute);
