import React from 'react';
import {
  HashRouter,
  Route,
} from 'react-router-dom';

import HomeRoute from './HomeRoute';
import PodcastRoute from './PodcastRoute';
import AboutUsRoute from './AboutUsRoute';

const Router = () => {
  return (
    <HashRouter>
      <>
        <Route exact path="/" component={HomeRoute} />
        <Route exact path="/podcasts" component={PodcastRoute} />
        <Route exact path="/about-us" component={AboutUsRoute} />
      </>
    </HashRouter>
  );
};

Router.propTypes = {};

export default Router;