import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Icon } from './../../components';
import {
  homeActions,
  homeSelectors
} from './../../redux/ducks/home';

const {
  closeNavOverlay
} = homeActions;

import {
  StyledOverlay,
  StyledOverlaBody,
  StyledSubNav,
  StyledCloseNavOverlay
} from './styled';

const closeBtn = '<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 600 12"><path stroke="#fff" stroke-width="2" d="M1 11L11 1M1 1l10 10"/></svg>';

const ListItem = ({txt, url}) => <li><a href={url}>{txt}</a></li>;

class NavOverlay extends PureComponent {
  constructor(props) {
    super(props);
    this.handleEscape = this.handleEscape.bind(this);
  }

  handleEscape (e) {
    e.keyCode === 27 &&
      this.props.closeNavOverlay();
  }

  componentDidMount() {
    window.addEventListener('keydown', this.handleEscape);
  }

  componentWillUnmount() {
    window.removeEventListener('keydown', this.handleEscape);
  }

  render () {
    return (
      <StyledOverlay>
        <StyledOverlaBody>
          <StyledCloseNavOverlay onClick={() => this.props.closeNavOverlay()}>
            <Icon
              src={closeBtn}
              raw={true}
            />
          </StyledCloseNavOverlay>
          <StyledSubNav>
            <div>
              <h2>Sections</h2>
              <ul>
                {['Home',
                  'News',
                  'Opinion',
                  'Meanwhile',
                  'Arts & Life',
                  'Podcasts',
                  'Videos',
                  'Galleries',
                  'Newsletter',
                  'Print Editions'].map((v, i) => <ListItem txt={v} key={i} />)}
              </ul>
            </div>
            <div>
              <h2>Multimedia Projects</h2>
              <ul>
                {[
                  'Mothers & Daughters',
                  'Generation P'
                ].map((v, i) => <ListItem txt={v} key={i} />)}
              </ul>
            </div>
          </StyledSubNav>
        </StyledOverlaBody>
      </StyledOverlay>
    );
  }
}

NavOverlay.propTypes = {
  closeNavOverlay: PropTypes.func,
  ui: PropTypes.object
};

ListItem.propTypes = {
  txt: PropTypes.string,
  url: PropTypes.string
};

const mapActionsToProps = { closeNavOverlay };

const mapStateToProps = state => ({
  ui: homeSelectors.getHomeUI(state),
});

export default connect(
  mapStateToProps,
  mapActionsToProps
)(NavOverlay);