import { createSelector } from 'reselect';

const reduceOptions = opts => opts && opts.reduce((acc, val) => {
  acc.push({
    value: val,
    label: val
  });
  return acc;
}, []);

const getHomeCollection = createSelector(
  state => state.home.collection,
  collection => collection
);

const getHomeUI = createSelector(
  state => state.home.ui,
  ui => ui
);

export {
  getHomeCollection,
  getHomeUI,
};