import TYPES from './types';

const homeRouteLoaded = () => ({ type: TYPES.HOME_ROUTE_LOADED });

const homeRouteLoadedSuccess = payload => ({
  type: TYPES.HOME_ROUTE_LOADED_SUCCESS,
  payload
});

const homeRouteLoadedFailute = payload => ({
  type: TYPES.HOME_ROUTE_LOADED_FAILURE,
  payload
});

const openNavOverlay = () => ({
  type: TYPES.NAV_OVERLAY_OPEN
});

const closeNavOverlay = () => ({
  type: TYPES.NAV_OVERLAY_CLOSE
});

const openModal = () => ({
  type: TYPES.MODAL_OPEN
});

const closeModal = () => ({
  type: TYPES.MODAL_CLOSE
});

export {
  homeRouteLoaded,
  homeRouteLoadedSuccess,
  homeRouteLoadedFailute,
  openNavOverlay,
  closeNavOverlay,
  openModal,
  closeModal
};
