import React from 'react';
import PropTypes from 'prop-types';
import { logoSVG } from './logoSVG';
import { Icon } from './../../components';
import {
  StyledAnchor,
  SubText
} from './styled';

const Logo = ({sub, clsName, ...rest}) => (
  <StyledAnchor href="/">
    <span>
      <Icon
        className={clsName}
        src={logoSVG}
        raw={true}
      />
    </span>
    {
      sub &&
        <SubText
          {...rest}
        >
          {sub}
        </SubText>
    }
  </StyledAnchor>
);

Logo.propTypes = {
  sub: PropTypes.string,
  clsName: PropTypes.string,
};

export default Logo;