import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { Icon } from './../../components';
import {
  StyledNavTrigger
} from './styled';

const svgStyles = {
  width: '16px',
  height: '16px',
  display: 'inline-block',
  verticalAlign: 'sub',
  backgroundColor: 'white'
};

const searchSVG = `
  <svg viewBox="0 0 16 16">
    <rect x="1" y="1" fill="#000" width="14" height="2" />
    <rect x="1" y="5" fill="#000" width="14" height="2" />
    <rect x="1" y="9" fill="#000" width="14" height="2" />
  </svg>
`;

class NavTrigger extends PureComponent {
  render () {
    const { onClick } = this.props;
    return (
      <StyledNavTrigger onClick={onClick}>
        <Icon src={searchSVG} style={svgStyles} />
      </StyledNavTrigger>
    );
  }
}

NavTrigger.propTypes = {
  onClick: PropTypes.func
};

export default NavTrigger;