import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import {
  StyledPodcastsGrid,
  StyledPodcastsGridItem,
  StyledBox,
  Col,
  Grid3,
  StyledPodcastItem
} from './styled';

const Item = ({
  name,
  img
}) => (
  <StyledPodcastItem>
    <figure>
      <img src={img} />
      <i>&reg;</i>
    </figure>
    <div>
      <p>{name}: lorem ipsum lorem ipsum lorem ipsum.</p>
    </div>
  </StyledPodcastItem>
);

Item.propTypes = {
  name: PropTypes.string,
  img: PropTypes.string
};

class Staff extends PureComponent {
  constructor (props) {
    super(props);
    this.state = {};
  }
  componentDidMount () {
    fetch('https://jsonplaceholder.typicode.com/users')
      .then(res => res.json())
      .then(users => this.setState({users}));
  }
  render () {
    return (
      <StyledPodcastsGrid>
        <StyledPodcastsGridItem>
          <Col>
            <h1>The Word&apos;s Worth</h1>
            <StyledBox>
              <div>
                <a href="https://www.themoscowtimes.com/author/michele-a-berdy">
                  <img src="https://static.themoscowtimes.com/image/320/91/b12eba308f6b4ba8aff80c96601d5ee6.jpg" alt="Word's Worth"/>
                </a>
              </div>
              <p>Michele A. Berdy is the Arts Editor and author of “The Russian Word’s Worth,” a collection of her columns.</p>
            </StyledBox>
          </Col>
          <Grid3>
            {this.state.users && this.state.users.map((v, i) => <Item img="https://static.themoscowtimes.com/image/article_640/2c/notebook-1840276_1280.jpg" key={i} name={v.name} />)}
          </Grid3>
        </StyledPodcastsGridItem>
        <StyledPodcastsGridItem>
          <Col>
            <h1>From Russia With News</h1>
            <StyledBox>
              <div>
                <a href="https://www.themoscowtimes.com/author/from-russia-with-news">
                  <img src="https://static.themoscowtimes.com/image/320/57/1200x1200bb-75.jpg" alt="From Russia With News"/>
                </a>
              </div>
              <p>From Russia With News is hosted by <a href="">Jonathan Brown</a> and produced by <a href="">Pjotr Sauer</a>. The episode was recorded and edited at CM Records Studios in central Moscow. Stay tuned for a new episode every Thursday on our site or here. Also available on iTunes, Google Podcasts, Spotify and other audio platforms.</p>
            </StyledBox>
          </Col>
          <Grid3>
            {this.state.users && this.state.users.map((v, i) => <Item img="https://static.themoscowtimes.com/image/article_640/2b/ca47f0586f4d46588eda47b49f007874.jpg" key={i} name={v.name} />)}
          </Grid3>
        </StyledPodcastsGridItem>
      </StyledPodcastsGrid>
    );
  }
}

Staff.propTypes = {};

export default Staff;
