import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import {
  Icon
} from './../../components/';

import {
  StyledModal,
  StyledModalBody,
  StyledModalContent
} from './styled';

const src = '<svg width="29" height="29"><path d="M20.13 8.11l-5.61 5.61-5.609-5.61-.801.801 5.61 5.61-5.61 5.61.801.8 5.61-5.609 5.61 5.61.8-.801-5.609-5.61 5.61-5.61" fill-rule="evenodd"></path></svg>';

const Modal = ({type, onClick, children}) => {
  const handleEscape = e => e.keyCode === 27 && onClick();
  useEffect(() => {
    window.addEventListener('keydown', handleEscape);
  });
  return (
    <StyledModal
      {...type}
    >
      <StyledModalBody>
        <section>
          <div>
            <button onClick={onClick}><Icon src={src} /></button>
            <StyledModalContent>
              {children}
            </StyledModalContent>
          </div>
        </section>
      </StyledModalBody>
    </StyledModal>
  );
};

Modal.propTypes = {
  onClick: PropTypes.func,
  type: PropTypes.string,
  children: PropTypes.array
};

export default Modal;
