import 'rxjs';
import 'rxjs/add/operator/mergeMap';
import api from './../../services/api';
import { homeActions } from './../ducks/home';
import TYPES from './../ducks/home/types';


const getHomeEpic = action$ => {
  return action$.ofType(TYPES.HOME_ROUTE_LOADED)
    .mergeMap(() => {
      return api.loadHomeData()
        .then(res => homeActions.homeRouteLoadedSuccess(res))
        .catch(err => ({type: 'HOME_ROUTE_NOT_LOADED', err}));
    });
};

export {
  getHomeEpic,
};