import TYPES from './types';
import { combineReducers, compose } from 'redux';
import { createReducer } from './../../utils';

const homeState = {};

const homeCollectionReducer = createReducer(homeState)({
  [TYPES.HOME_ROUTE_LOADED_SUCCESS]: (state, payload) => {
    return { ...state, ...payload };
  },
  [TYPES.HOME_ROUTE_LOADED_FAILURE]: (state, payload) => {
    return { ...state, ...payload };
  }
});

const uiReducer = createReducer({
  homeLoaded: false,
  navOverlay: false,
  genericModal: false
})({
  [TYPES.HOME_ROUTE_LOADED_SUCCESS]: state => {
    return { ...state, homeLoaded: true };
  },
  [TYPES.NAV_OVERLAY_OPEN]: state => {
    return { ...state, navOverlay: true };
  },
  [TYPES.NAV_OVERLAY_CLOSE]: state => {
    return { ...state, navOverlay: false };
  },
  [TYPES.MODAL_OPEN]: state => {
    return { ...state, genericModal: true };
  },
  [TYPES.MODAL_CLOSE]: state => {
    return { ...state, genericModal: false };
  }
});

export default combineReducers({
  collection: homeCollectionReducer,
  ui: uiReducer
});
