import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import {
  Header,
  Nav,
  RowFlex,
  Footer,
  Staff
} from './../containers';

import {
  homeActions,
  homeSelectors
} from './../redux/ducks/home';


class AboutUsRoute extends PureComponent {
  componentDidUpdate () {
    const { navOverlay } = this.props.ui;
    {
      document.body.style.overflow = navOverlay ? 'hidden' : '';
    }
  }
  render () {
    return (
      <>
        <section className="container">
          <Header {...this.props} />
          <Nav />
        </section>
        <section className="container">
          <RowFlex cls="row-flex">
            <div className="col">
              <article className="page">
                <header className="page__header">
                  <h1>About Us</h1>
                </header>
                <section className="page__intro">
                  The Moscow Times is Russia’s leading, independent English-language media outlet. From our Moscow newsroom, we provide readers across the world with breaking news, engaging stories and balanced journalism about the largest country on Earth.
                </section>
                <section className="page__content">
                  <p>We pride ourselves on adhering to the highest journalistic standards. All editorial decisions are made independently by our team of editors and reporters, a practice that has been in place since the publication was founded in 1992.</p>
                  <h3>Our Team</h3>
                  <Staff />
                </section>
              </article>
            </div>
            <div className="col-auto hidden-sm-down">
              <aside className="sidebar">
                <section className="sidebar__section">
                  <div className="banner">
                    <div style={{overflow: 'hidden'}} className="banner-inner">
                    </div>
                  </div>
                </section>
              </aside>
            </div>
          </RowFlex>
        </section>
        <section className="container">
          <Footer />
        </section>
      </>
    );
  }
}

AboutUsRoute.propTypes = {
  ui: PropTypes.object
};

const mapActionsToProps = {...homeActions};

const mapStateToProps = state => ({
  ui: homeSelectors.getHomeUI(state),
});

export default connect(
  mapStateToProps,
  mapActionsToProps
)(AboutUsRoute);
