import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

// TODO: abstract survey component

import {
  Header,
  Nav,
  RowFlex,
  Footer,
  Developing
} from './../containers';

import {
  Survey
} from './../components';

import {
  homeActions,
  homeSelectors
} from './../redux/ducks/home';

const bneAnnouncment = {
  headliner: 'Announcement',
  text: 'Announcement Text'
};

class HomeRoute extends PureComponent {
  componentDidMount () {
    this.props.homeRouteLoaded();
  }
  componentDidUpdate () {
    const { navOverlay } = this.props.ui;
    {
      document.body.style.overflow = navOverlay ? 'hidden' : '';
    }
  }
  render () {
    return (
      <>
        <section className="container">
          <Header {...this.props }/>
          <Nav />
        </section>
        <section className="container">
          <RowFlex cls="row-flex">
            <div className="col" style={{height: '100vw'}}>
              <div style={{background: '#ccc', height: '100vw'}}></div>
            </div>
            <div className="col-auto hidden-sm-down">
              <aside className="sidebar">
                <section className="sidebar__section">
                  <div className="banner">
                    <div style={{overflow: 'hidden'}} className="banner-inner">
                      <a target="_blank" rel="noopener noreferrer" className="offlineBox" href="https://offline.themoscowtimes.com/#/theater">
                        <span>
                          If you like The Moscow Times online, you&apos;ll love The Moscow Times Offline.<br /> Click here for our first event.
                        </span>
                        <sub>
                          The Moscow Times Team
                        </sub>
                      </a>
                    </div>
                  </div>
                  {/* <div className="banner">
                    <div style={{overflow: 'hidden'}} className="banner-inner">
                      <Survey {...bneAnnouncment} />
                    </div>
                  </div> */}
                </section>
              </aside>
            </div>
          </RowFlex>
        </section>
        <section className="container" style={{marginTop: '24px'}}>
          <Developing />
        </section>
        <section className="container">
          <Footer />
        </section>
      </>
    );
  }
}

HomeRoute.propTypes = {
  homeRouteLoaded: PropTypes.func,
  ui: PropTypes.object
};

const mapActionsToProps = { ...homeActions };

const mapStateToProps = state => ({
  collection: homeSelectors.getHomeCollection(state),
  ui: homeSelectors.getHomeUI(state),
});

export default connect(
  mapStateToProps,
  mapActionsToProps
)(HomeRoute);
