import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import {
  StyledGrid,
  StyledInfo,
  StyledItem
} from './styled';

class Staff extends PureComponent {
  constructor (props) {
    super(props);
    this.state = {};
  }
  componentDidMount () {
    fetch('https://jsonplaceholder.typicode.com/users')
      .then(res => res.json())
      .then(users => this.setState({users}));
  }
  render () {
    console.log(this.state);
    return (
      <StyledGrid>
        {this.state.users && this.state.users.map((v, i) => {
          return (
            <StyledItem key={i}>
              <a href="#">
                <img src="https://static.themoscowtimes.com/image/320/91/b12eba308f6b4ba8aff80c96601d5ee6.jpg" />
              </a>
              <StyledInfo>
                <h4>{v.name}</h4>
                <p>{v.company.catchPhrase}</p>
                <p>{v.company.bs}</p>
                <p><a href="#">{v.email}</a></p>
              </StyledInfo>
            </StyledItem>
          );
        })}
      </StyledGrid>
    );
  }
}

Staff.propTypes = {};

export default Staff;
