import React from 'react';
import PropTypes from 'prop-types';
import {
  StyledSurvey,
  StyledBody
} from './styled';

const Survey = ({
  headliner,
  greetA,
  greetB,
  text,
  btnTxt,
  btnSubTxt
}) => (
  <StyledSurvey>
    <span>{headliner}</span>
    <StyledBody>
      {
        greetA &&
          <h2>
            <span>{greetA}</span>
            {greetB && <span>{greetB}</span>}
          </h2>
      }
      <p>{text}</p>
    </StyledBody>
    {
      btnTxt &&
        <strong>
          <span>{btnTxt}</span>
          {btnSubTxt && <span>{btnSubTxt}</span>}
        </strong>
    }
  </StyledSurvey>
);

Survey.propTypes = {
  headliner: PropTypes.string,
  greetA: PropTypes.string,
  greetB: PropTypes.string,
  text: PropTypes.string,
  btnTxt: PropTypes.string,
  btnSubTxt: PropTypes.string,
};

export default Survey;