import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {
  Link
} from 'react-router-dom';
import {
  homeActions,
  homeSelectors
} from './../../redux/ducks/home';
import {
  SearchToggle,
  NavTrigger
} from './../../components';
import {
  StyledNav,
  StyledNavList
} from './styled';

import NavOverlay from './NavOverlay';

const {
  openNavOverlay
} = homeActions;

const ListItem = ({txt, url}) => <li><Link to={url}>{txt}</Link></li>;

class Nav extends PureComponent {
  render () {
    const { navOverlay } = this.props.ui;
    return (
      <>
      <StyledNav>
        <NavTrigger onClick={() => this.props.openNavOverlay()} />
        <StyledNavList>
          {
            [
              {url: 'news', txt: 'News'},
              {url: 'opinion', txt: 'Opinion'},
              {url: 'meanwhile', txt: 'Meanwhile'},
              {url: 'arts-and-life', txt: 'Arts & Life'},
              {url: 'podcasts', txt: 'Podcasts'}
            ]
              .map((v, i) => <ListItem key={i} txt={v.txt} url={v.url} />)
          }
        </StyledNavList>
        <SearchToggle />
      </StyledNav>
      {navOverlay && <NavOverlay />}
      </>
    );
  }
}

Nav.propTypes = {
  openNavOverlay: PropTypes.func,
  ui: PropTypes.object
};

ListItem.propTypes = {
  url: PropTypes.string,
  txt: PropTypes.string,
};

const mapActionsToProps = { openNavOverlay };

const mapStateToProps = state => ({
  ui: homeSelectors.getHomeUI(state),
});

export default connect(
  mapStateToProps,
  mapActionsToProps
)(Nav);